import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';

import { CompensatoryOffService, CompOff } from 'modules/comp-off/comp-off.service';
import { STATUS } from 'config/constants/compensatory-off';
import { Access } from 'core/providers/access.service';
import { ExaTask } from '../../../core/providers/exatask.service';
import { EmployeeLeavePolicyService } from '../../employee/employee-leave-policy/employee-leave-policy.service';
import { User } from '../../../core/providers/user.service';

@Component({
  selector: 'app-list-comp-off',
  templateUrl: './list-comp-off.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [CompensatoryOffService, EmployeeLeavePolicyService]
})
export class ListCompOffComponent implements OnInit {

  compoffs: CompOff[] = [];

  applyAllowed: boolean = false;
  reviewAllowed: boolean = false;
  compOffPolicyExists: boolean = false;

  loading: boolean = false;
  cancelling: Array<Boolean>;

  constructor(
    private compOffService: CompensatoryOffService,
    private employeeLeavePolicyService: EmployeeLeavePolicyService,
    private user: User,
    private access: Access,
    public exatask: ExaTask
  ) {

    this.exatask.setTitle('Compensatory-Off List');
    this.cancelling = new Array<Boolean>();
  }

  async ngOnInit() {

    this.applyAllowed = await this.access.hasAccess({
      module: 'compensatory-off',
      entity: 'compensatory-off',
      action: 'create'
    });

    this.reviewAllowed = await this.access.hasAccess({
      module: 'compensatory-off',
      entity: 'compensatory-off',
      action: 'review'
    });

    this.employeeLeavePolicyService.getEmployeeLeavePolicy(this.user.user_id).then(policy => {

      if (policy) {
        this.compOffPolicyExists = true;
      } else {
        this.applyAllowed = false;
      }
    });

    this.loadCompOffs();
  }

  loadCompOffs() {

    const self = this;
    this.loading = true;
    this.compOffService.getCompOffs().then(compoffs => {

      self.loading = false;
      self.compoffs = compoffs;
    });
  }

  cancelCompOff(index) {

    const leave_id = this.compoffs[index]._id;
    const self = this;

    this.cancelling[index] = true;
    this.compOffService.cancel(leave_id).then(response => {

      self.compoffs[index].comp_off_status = STATUS.DELETED;
      self.cancelling[index] = false;
    });
  }

  getStatusBadge(status) {

    switch (status) {

      case STATUS.APPROVED:
      case STATUS.AVAILED:
        return 'badge-success';
      case STATUS.REJECTED:
        return 'badge-danger';
      case STATUS.DELETED:
      case STATUS.EXPIRED:
        return 'badge-dark';
      default:
        return 'badge-default';
    }
  }
}
