import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthenticationGuard } from 'core/services/authentication.service';
import { AuthorizationGuard } from 'core/services/authorization.service';
import { ListCompOffComponent } from './list-comp-off/list-comp-off.component';
import { ApplyCompOffComponent } from './apply-comp-off/apply-comp-off.component';
import { ReviewCompOffComponent } from './review-comp-off/review-comp-off.component';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Comp-Off'
    },
    children: [
      {
        path: '',
        redirectTo: 'list'
      },
      {
        path: 'list',
        data: {
          title: 'List',
          access: {
            module: 'compensatory-off',
            entity: 'compensatory-off',
            action: 'find'
          }
        },
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        component: ListCompOffComponent
      },
      {
        path: 'apply',
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        data: {
          title: 'Apply',
          access: {
            module: 'compensatory-off',
            entity: 'compensatory-off',
            action: 'create'
          }
        },
        component: ApplyCompOffComponent
      },
      {
        path: 'review',
        data: {
          title: 'Review',
          access: {
            module: 'compensatory-off',
            entity: 'compensatory-off',
            action: 'find'
          }
        },
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        component: ReviewCompOffComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CompensatoryOffRoutingModule { }
