import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';
import { find, matchesProperty } from 'lodash';

import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';
import { ApplyCompOff, CompensatoryOffService } from '../comp-off.service';
import { DatePipe } from '@angular/common';
import { ExaTask } from '../../../core/providers/exatask.service';
import { EmployeeService } from '../../employee/employee.service';
import { User } from '../../../core/providers/user.service';
import { EmployeeBasic } from '../../leave/leave.service';
import { Name } from '../../user/profile/profile.service';

class UserSearchItem {

  user_id: string;
  name: string;
  email_id: string;
}

const error_map = {
  date: 'Date',
  summary: 'Summary'
};

@Component({
  selector: 'app-apply-comp-off',
  templateUrl: './apply-comp-off.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [CompensatoryOffService, EmployeeService, DatePipe]
})
export class ApplyCompOffComponent implements OnInit {
  @ViewChild('errorModal') errorModal: ErrorModalComponent;
  @ViewChild('successModal') successModal: SuccessModalComponent;

  compoff: ApplyCompOff;
  user_list: UserSearchItem[] = [];
  notify_users: Array<string> = [];

  dateConfig: Object;

  loading_users: boolean = false;
  error: string;
  invalid_attributes: string[];

  constructor(
    private compoffService: CompensatoryOffService,
    private employeeService: EmployeeService,
    private user: User,
    private datePipe: DatePipe,
    public exatask: ExaTask
  ) {

    this.exatask.setTitle('Apply Compensatory-Off');
    this.compoff = new ApplyCompOff();

    this.dateConfig = {
      dateInputFormat: 'YYYY-MM-DD',
      containerClass: 'theme-blue',
      showWeekNumbers: false,
      maxDate: new Date()
    };
  }

  async ngOnInit() {

    this.__loadUsers();
  }

  private __validate() {
    return true;
  }

  private __loadUsers() {

    const self = this;
    this.loading_users = true;
    this.employeeService.getEmployees(null, 0, 0, ['_id', 'email_id', 'name'])
      .then(response => {

        this.user_list = response.users.filter(user => {

          if (self.user.user_id !== user._id) {
            return user;
          }

        }).map(user => {

          const item = new UserSearchItem();
          item.user_id = user._id;
          item.email_id = user.email_id;
          item.name = Name.getFullName(user.name);
          return item;
        });
        this.loading_users = false;

      }).catch(err => {

      this.error = err.error.message.text;
      this.errorModal.show();
    });
  }

  private __sanitizeCompoff(compOff) {

    if (compOff.date !== undefined) {
      compOff.date = this.datePipe.transform(compOff.date, 'yyyy-MM-dd');
    }

    if (this.notify_users.length > 0) {

      compOff.notify_users = [];
      for (const user_id of this.notify_users) {

        const user = find(this.user_list, matchesProperty('user_id', user_id));
        const employee = new EmployeeBasic();
        employee.email_id = user.email_id;
        employee.name = user.name;
        compOff.notify_users.push(employee);
      }
    }

    return compOff;
  }

  applyCompOff() {

    if (this.__validate()) {

      let compOff = JSON.parse(JSON.stringify(this.compoff));
      compOff = this.__sanitizeCompoff(compOff);
      this.error = '';
      this.invalid_attributes = [];

      this.compoffService.apply(compOff).then(data => {

        this.successModal.show('/comp-off');

      }).catch(err => {

        this.error = err.error.message.text;
        if (err.error.invalid_attributes) {
          for (const key in err.error.invalid_attributes) {
            if (err.error.invalid_attributes.hasOwnProperty(key)) {
              this.invalid_attributes.push(error_map[key] ? error_map[key] : err.error.invalid_attributes[key]);
            }
          }
        }
        this.errorModal.show();
      });
    }
  }

  filterUsers(term: string, item: UserSearchItem) {

    const regex = new RegExp(term, 'i');
    return item.email_id.match(regex) || item.name.match(regex);
  }
}
