import { Injectable } from '@angular/core';
import { Api } from 'core/services/api.service';
import {
  Name,
  ContactNumber,
  SocialSecurity,
  EmergencyContact,
  Qualification
} from 'modules/user/profile/profile.service';
import { Department } from 'modules/organization/department/department.service';
import { Address } from 'shared/components/address/address.component';
import { clone } from 'lodash';

export class UserReference {

  supervisor_id: string;
  name: string;
}

export class Employee {

  _id: string;
  name: Name = new Name();
  email_id: string = '';
  employee_id: string;
  employee_type: number;
  department: string;
  designation: string;
  supervisor: string;
  user_status: number;
  is_supervisor?: boolean;
  is_chief_executive?: boolean;
  is_super_admin: boolean;
}

export class EmployeeList {

  _id: string;
  name: Name = new Name();
  email_id: string = '';
  employee_id: string;
  employee_type: number;
  department?: Department;
  designation: string;
  supervisor?: UserReference;
  user_status: number;
  is_supervisor?: boolean;
  is_chief_executive?: boolean;
  is_super_admin?: boolean;
}

export class Leave {

  leave_type: string;
  leaves: number;
}

export class EmployeeDetails {

  _id: string;
  name: Name;
  full_name: string;
  employee_type: number;
  email_id: string;
  employee_id: string;
  profile_picture: string;
  department?: Department;
  designation: string;
  supervisor?: UserReference;
  user_status: number;
  is_supervisor?: boolean;
  is_chief_executive?: boolean;
  is_super_admin?: boolean;
  contact_number?: string;
  isd_code?: string;
  date_of_birth?: string;
  date_of_joining?: string;
  last_login?: string;
  timezone?: string;
  added_by?: UserReference;
  gender?: string;
  marital_status?: string;
  permanent_address?: Address;
  correspondence_address?: Address;
  alternate_email_ids?: Array<String>;
  alternate_contact_numbers?: Array<ContactNumber>;
  social_security?: Array<SocialSecurity>;
  emergency_contacts?: Array<EmergencyContact>;
  qualification?: Array<Qualification>;
  leaves?: Array<Leave>;
}

@Injectable()
export class EmployeeService extends Api {

  private _base_url = '/employees';
  private _file_url = '/files';

  getEmployees(
    query: Map<string, any>,
    page?: number,
    limit?: number,
    fields?: Array<string>
  ): Promise<{ count: number, users: EmployeeList[] }> {

    if (!query) {
      query = new Map<string, any>();
    }
    if (page > 0) {
      query.set('page', page);
    }
    if (limit > 0) {
      query.set('limit', limit);
    }
    if (fields) {
      query.set('fields', fields.join(','));
    }

    return this.get(this._base_url, query)
      .then(response => {
        return {
          count: response['count'],
          users: response['data'] as EmployeeList[]
        };
      });
  }

  postEmployee(user: any) {

    return this.post(this._base_url, user)
      .then(response => response);
  }

  putEmployee(id: string, user: any) {

    return this.put(this._base_url + '/' + id, user)
      .then(response => response);
  }

  getEmployeeDetails(id: string) {

    return this.get(this._base_url + '/' + id)
      .then(response => response);
  }

  markAsSupervisor(employee_id: string) {

    return this.patch(this._base_url + '/supervisors/' + employee_id)
      .then(response => response);
  }

  unmarkAsSupervisor(employee_id: string) {

    return this.delete(this._base_url + '/supervisors/' + employee_id)
      .then(response => response);
  }

  markAsChiefExecutive(employee_id: string) {

    return this.patch(this._base_url + '/chiefexecutives/' + employee_id)
      .then(response => response);
  }

  unmarkAsChiefExecutive(employee_id: string) {

    return this.delete(this._base_url + '/chiefexecutives/' + employee_id)
      .then(response => response);
  }

  markAsSuperAdmin(employee_id: string) {

    return this.patch(this._base_url + '/superadmins/' + employee_id)
      .then(response => response);
  }

  unmarkAsSuperAdmin(employee_id: string) {

    return this.delete(this._base_url + '/superadmins/' + employee_id)
      .then(response => response);
  }

  resetPassword(user_id: string) {

    return this.patch(this._base_url + '/reset/' + user_id)
      .then(response => response);
  }

  uploadBulkEmployee(file: File) {

    const formData = new FormData();
    formData.append('file', file);

    const query_params = new Map<string, any>();
    query_params.set('type', 'bulk_employee');

    let headers = clone(this.headers);
    headers = headers.delete('Content-Type');
    const config = new Map<string, any>();
    config.set('headers', headers);

    return this.post(this._file_url + '/upload', formData, query_params, config)
      .then(response => {

        const body = {
          file: response.file
        };

        return this.post(this._file_url + '/parse', body, query_params)
          .then(data => data);
      });
  }

  exportEmployees(query?: Map<string, any>, fields?: Array<string>) {

    if (!query) {
      query = new Map<string, any>();
    }
    if (fields) {
      query.set('fields', fields.join(','));
    }
    query.set('export', 1);

    return this.get(this._base_url, query)
      .then(data => data);
  }
}
