import { Component, ViewEncapsulation, OnInit, ViewChild } from '@angular/core';
import { STATUS } from 'config/constants/leave';
import { CompensatoryOffService, CompOff } from '../comp-off.service';
import { Access } from 'core/providers/access.service';
import { ExaTask } from '../../../core/providers/exatask.service';
import { ModalDirective } from 'ngx-bootstrap';
import { find, matchesProperty } from 'lodash';

enum Operation {
  APPROVE = 1, REJECT
}

@Component({
  selector: 'app-review-comp-off',
  templateUrl: './review-comp-off.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [CompensatoryOffService]
})
export class ReviewCompOffComponent implements OnInit {
  @ViewChild('confirmModal') confirmModal: ModalDirective;

  compoffs: CompOff[] = [];
  compoff_id: string;
  compoff: CompOff;

  reviewed: Array<Boolean>;
  approving: Array<Boolean>;
  rejecting: Array<Boolean>;
  loading: boolean = false;

  approveAllowed: boolean;
  rejectAllowed: boolean;

  operation: Operation = Operation.APPROVE;
  reject_reason: string = '';

  constructor(
    private compoffService: CompensatoryOffService,
    private access: Access,
    public exatask: ExaTask
  ) {

    this.reviewed = [];
    this.approving = [];
    this.rejecting = [];
    this.exatask.setTitle('Compensatory-Off Review List');
  }

  async ngOnInit() {

    this.loadCompOffs();

    this.approveAllowed = await this.access.hasAccess({
      module: 'compensatory-off',
      entity: 'review',
      action: 'approve'
    });

    this.rejectAllowed = await this.access.hasAccess({
      module: 'compensatory-off',
      entity: 'review',
      action: 'reject'
    });
  }

  private loadCompOffs() {

    const self = this;
    const query = new Map<string, any>();
    query.set('comp_off_status', STATUS.APPLIED);

    this.loading = true;

    this.compoffService.getReviewCompOffs(query).then(compoffs => {

      self.loading = false;
      self.compoffs = compoffs;
    });
  }

  approve(comp_off_id) {

    this.approving[comp_off_id] = true;
    this.compoffService.approve(comp_off_id).then(response => {

      this.reviewed[comp_off_id] = true;
      this.approving[comp_off_id] = false;
    });
  }

  reject(comp_off_id) {

    this.rejecting[comp_off_id] = true;
    this.compoffService.reject(comp_off_id, this.reject_reason).then(response => {

      this.reviewed[comp_off_id] = true;
      this.rejecting[comp_off_id] = false;
      this.reject_reason = '';
    });
  }

  compOffConfirmation(operation, compoff_id) {

    this.operation = operation;
    this.compoff_id = compoff_id;
    this.compoff = find(this.compoffs, matchesProperty('_id', compoff_id));
    this.reject_reason = '';
    this.confirmModal.show();
  }
}
