import { Injectable } from '@angular/core';

import { Api } from 'core/services/api.service';

export class EmployeeBasic {

  user_id: string;
  email_id: string;
  name: string;
}

export class ApplyCompOff {

  date: string = '';
  summary: string = '';
  notify_users: Array<EmployeeBasic>;
}

export class CompOff {

  _id: string;
  name: string;
  user_id: string;
  date: string;
  summary: string;
  expiry_date: string;
  reviewed_by: EmployeeBasic;
  comp_off_status: number;
}

@Injectable()
export class CompensatoryOffService extends Api {

  private _base_url: string = '/compoffs';

  apply(compOff: ApplyCompOff): Promise<Object> {

    return this.post(this._base_url, compOff)
      .then(response => response);
  }

  getCompOffs(query?: Map<string, any>): Promise<CompOff[]> {

    return this.get(this._base_url, query)
      .then(response => response['data'] as CompOff[]);
  }

  getReviewCompOffs(query?: Map<string, any>): Promise<CompOff[]> {

    return this.get(this._base_url + '?review=true', query)
      .then(response => response['data'] as CompOff[]);
  }

  approve(comp_off_id: string): Promise<Object> {

    return this.patch(this._base_url + '/review/' + comp_off_id, {})
      .then(response => response);
  }

  reject(comp_off_id: string, reject_reason: string): Promise<Object> {

    return this.put(this._base_url + '/review/' + comp_off_id, {reason: reject_reason})
      .then(response => response);
  }

  cancel(compoff_id: string): Promise<Object> {

    return this.delete(this._base_url + '/' + compoff_id)
      .then(response => response);
  }
}
